@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
@media only screen and (min-width: 800px){

  .proj_sec{
    position: relative;
  }
  .title{
    font-weight: 900;
    font-size: 9rem;
    margin-left: 5rem;
    color: rgb(81, 81, 81);
  }
  .container{
    display: flex;
    top: 50%;
    left: 50%;
    /* flex-direction: column; */
    width: 60rem;
    box-shadow: 1rem 1rem 2rem 2rem black;
    /* background-color: aqua; */
    height: 23rem;
    position: absolute;
    overflow: hidden;
  }
  .text_slide{
    transform: translateY(-200%);
    transition: all 1s linear;
    position: relative;
  }
  .proj_title{
    font-size: 1.6rem;
    font-weight: 900;
    color: #B47124;
  }
  .proj_info{
    /* width: 150%; */
    height: 100%;
    /* background-color: aquamarine; */
    margin: 1rem;
    /* position: relative; */
  }
  .proj_img_cont{
    height: 100%;
    transition: all 1s linear;
    /* transform: translateY(-100%); */
  }
  .proj_img{
    width: 100%;
    height: 50%;
    background-position: left;
    background-size: cover;
  }
  button{
    border: none;
    width: 2rem;
    height: 2rem;
    background-color: white;
    color: black;
  }
  .left_but{
    display: none;
  }
  .right_but{display: none;}
  .up_but{
    position: absolute ;
    z-index: 100;
    /* top: 42%;
    left: 26.8%;} */
    top: 42%;
    left: 16.1rem;}
    .down_but{
      position: absolute;
      z-index: 100;
      left: 18rem;
      top: 50%;
    }
    .tools{
      display: flex;
      flex-wrap: wrap;
      
    }
    .tools>span{
      background-color: #B47124;
      margin: 0.3rem;
      padding: 0.1rem;
    }
  }
    
@media only screen and (max-width: 700px){
  .proj_sec{
    display: flex;
    width: 100vw;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .title{
    /* margin-left: 5rem; */
    font-size: 6rem;
  }
  .container{
    flex-direction: column;
    border-top: 4px solid rgb(77, 76, 76);
    padding-top: 1rem;
    width: 30rem;
    height: 45rem;
    top: 100%;
  left: 10%;
  /* background-color: aqua; */
overflow: hidden;
  }
  .text_slide{
    display: flex;
    width: 300%;
    transition: all 0.3s ease;
    transform: translateX(-60rem);
    /* transform: translateX(-30rem); */
  }
  .proj_info{
    width: 35rem;
    height: 20rem;


  }
  .proj_img_cont{
    display: flex;
    width: 310%;
    transition: all 0.3s ease;

    /* transform: translateX(-70rem); */
  }
  .proj_img{
    width: 100%;

  }
  .proj_title{
    font-size: 2rem;
    font-weight: 900;
    color: #B47124;
  }
  .tools{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.5rem;
    
  }
  .tools>span{
    background-color: #B47124;
    margin: 0.3rem;
    padding: 0.1rem;
  }
  button{
    border: none;
    width: 2rem;
    height: 2rem;
    background-color: white;
    color: black;
    font-size: 1.5rem;
  }
  .up_but{
    display: none;
  }
  .down_but{
    display: none;
  }
  .right_but{
    position: absolute;
    top: 100%;
    left: 50%;
    width: 4rem;
    height: 3rem;
    display: inline;
  }
  .left_but{
    position: absolute;
    top: 100%;
    right: 51%;
    width: 4rem;
    height: 3rem;
    display: inline;
  }
}